body, html {
  background: linear-gradient(167.96deg, #F4F9FF 0%, #E0EDFB 100%);
  height: 100%;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  color: #4a4e71;
  font-size: 20px;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
}

.auth__content {
  text-align: center;
  padding: 2em;
  width: 100%;
  max-width: 500px;
}

.auth__input {
  padding: 1em;
  width: 100%;
  box-sizing: border-box;
  font-size: 1em;
  border-radius: 10px;
  border: 0;
  outline: red;
}

.auth__input:not(:last-child) {
  margin-bottom: 0.8em;
}

.auth__input::placeholder {
  color: #6f91bc;
}

.auth__input:focus {
  /* border: 2px #161d51 solid; */
  box-shadow: inset 0 0 0 2px #161d51;
}

.auth__input.auth__input_valid {
  box-shadow: inset 0 0 0 2px #5CD6C0;
  background-color: #effbf9;
  color: #5CD6C0;
}

.auth__input.auth__input_invalid {
  box-shadow: inset 0 0 0 2px #ED5F59;
  background-color: #fdefee;
  color: #ED5F59;
}

.validation {
  text-align: left;
  padding: 0 1em;
  margin-bottom: 0.8em;
}

.validation_hidden {
  display: none;
}

.validation__item:not(:last-child) {
  margin-bottom: 0.5em;
}

.auth__submit {
  border-top-left-radius: 1.5em 50%;
  border-bottom-left-radius: 1.5em 50%;
  border-top-right-radius: 1.5em 50%;
  border-bottom-right-radius: 1.5em 50%;
  padding: 1em;
  width: 80%;
  border: 0;
  background: linear-gradient(130deg, #70c3ff, #4f6eff);
  color: white;
  font-size: 1.1em;
  font-weight: bold;
}

.auth__input-with-icon {
  position: relative;
}

.password-toggle {
  border: 0;
  background-color: transparent;
  background-size: cover;
  width: 2em;
  height: 2em;
  padding: 0;
  position: absolute;
  top: 1.3em;
  right: 1em;
}

.password-toggle_closed {
  background-image: url(/public/images/closed-eye.svg);
}

.password-toggle_open {
  background-image: url(/public/images/open-eye.svg);
}

.validation__item-invalid {
  color: #ED5F59;
}

@media (max-width: 800px) {
  body, html {
    font-size: 16px;
  }

  .auth__content {
    padding: 1em;
  }

  .password-toggle {
    top: 0.9em;
  }
}
